import { services } from "@miq/adminjs";

export const docufileServices = {
    postFile(file = isRequired("file")) {
        let name = file.name;
        if (name.length >= 300) {
            return Promise.reject({ files: "File name is too long" });
        }

        const fd = new FormData();
        fd.append("src", file, name);

        return services.post(`docufiles/`, fd, {
            headers: { "content-type": "multipart/form-data" },
        });
    },

    delete(docFileSlug, docSlug) {
        return services.delete(`docufiles/${docFileSlug}/?document=${docSlug}`);
    },
};
