import { useContext } from "react";
import { Icons, ConfirmIconButton, Button, ToastCtx } from "@miq/components";

import { docufileServices } from "./utils";

export const FileDeleteIconButton = ({ document, file, ...props }) => {
    const toast = useContext(ToastCtx);

    if (!document || document.is_read_only) return null;

    const handleDelete = () =>
        docufileServices
            .delete(file.slug, document.slug)
            .then((data) => {
                props.onFileDelete({ ...data, slug: file.slug });
                toast.success({ message: "File deleted." });
            })
            .catch((err) => {
                // console.log(err);
                toast.error({ message: "Something went wrong." });
            });

    return (
        <ConfirmIconButton
            disabled={document.is_read_only}
            Icon={Icons.Trash}
            render={() => {
                return <div className="p-3">Are you sure?</div>;
            }}
            renderHeader={() => <div className="flex-1 fw-bold">Delete file {`${document.title_truncated}`}</div>}
            renderFooter={({ setOpen }) => {
                return (
                    <div className="flex-1">
                        <Button onClick={() => setOpen(false)} className="me-1">
                            Annuler
                        </Button>
                        <Button className="btn-danger" onClick={handleDelete}>
                            Delete
                        </Button>
                    </div>
                );
            }}
        />
    );
};
