import { useContext, useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';

import Form, { useForm } from '@miq/form';
import { AdminView } from '@miq/adminjs';
import { addForwardSlash } from '@miq/utils';
import { Icons, IconNavLink, ToastCtx } from '@miq/components';

import './update-view.scss';

import { BackToStaffDocumentsLink } from '../../components';
import DocuFileUploadButton from '../../docufile/UploadButton';
import { DocuFileList } from '../../docufile';
import { docServices } from '../utils';
import {
  DocumentTitleInput,
  DocumentArchiveIconButton,
  DocumentSubmitIconButton,
  DocumentDeleteIconButton,
} from '../components';
// import DocumentShareModal from './ShareModal';
import DocumentUpdateForm from './UpdateForm';

const Actions = (props) => {
  const { docSlug, doc, toast } = props;
  const [isOpen, setOpen] = useState(false);

  const handleArchive = (e) => {
    const url = `${addForwardSlash(props.match.url)}view/`;
    if (doc.status === 'ARCHIVED') return props.history.push(url);

    docServices
      .archive(docSlug)
      .then(() => {
        props.history.push(url);
      })
      .catch(() => {
        toast.error({ message: 'Oops! Something went wrong.' });
      });
  };

  const { path } = props.match;
  const url = addForwardSlash(props.match.url);

  return (
    <div className="document-update-view-actions">
      <div className="d-flex me-2">
        <DocumentDeleteIconButton
          {...props}
          {...{ docSlug }}
          label="Supprimer"
          className="btn-secondary-4 me-1"
          onSuccess={() => props.history.push(path.replace(':docSlug/', ''))}
        />

        <DocumentSubmitIconButton {...{ doc }} onClick={handleArchive} className="btn-primary-3" />
        <DocumentArchiveIconButton {...{ doc }} onClick={handleArchive} className="btn-primary-3" />
      </div>

      <div>
        {/* <IconNavLink to={`${path}share/`}>Par</NavLink> */}
        <IconNavLink
          to={`${url}share/`}
          // onClick={() => setOpen(true)}
          disabled={doc.is_read_only}
          Icon={Icons.Share}
          className="btn-primary"
          label="Partager"
          title="Partager ce document avec des personnes"
        />

        {/* {!doc.is_read_only && isOpen && <DocumentShareModal {...props} {...{ setOpen, isOpen }} />} */}
      </div>
    </div>
  );
};

/**
 *
 * @param {*} props
 * @returns
 */

export default function DocumentUpdateView(props) {
  const { docSlug } = props.match.params;

  const [doc, setDoc] = useState(null);
  const [error, setError] = useState(null);
  const toast = useContext(ToastCtx);

  const form = useForm({ title: '', reference: '', description: '' });
  const { setValues } = form;

  useEffect(() => {
    docServices
      .detail(docSlug)
      .then((data) => {
        setDoc(data);
        if (data.is_read_only) return;

        setValues({
          title: data.title || '',
          reference: data.reference || '',
          description: data.description || '',
        });
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status === 404) {
            setError('notfound');
            return;
          }
        }

        setError('error');
      });
  }, [docSlug, setValues]);

  if (error) {
    return (
      <div>
        <h1>Ce document n'existe pas</h1>
      </div>
    );
  }

  if (!doc || !doc.slug) return null;

  if (doc.is_read_only) return <Redirect to={`${addForwardSlash(props.match.url)}view/`} />;

  /**
   *
   * @param {Object} param0
   * @returns
   */
  const handleTextInputSave = ({ name, value }) => {
    if (!doc || doc.is_read_only) return;

    docServices
      .patch(docSlug, { [name]: value }, { [name]: doc[name] })
      .then((data) => {
        // console.log(data);
      })
      .catch((err) => {});
  };

  const addFile = ({ slug }) =>
    docServices
      .patchFile(docSlug, { action: 'add', files: [slug] })
      .then((data) => {
        setDoc({ ...doc, ...data });
      })
      .catch((err) => {});

  const removeFile = ({ slug }) =>
    setDoc({
      ...doc,
      files: doc.files.filter((i) => i !== slug),
      files_data: doc.files_data.filter((i) => i.slug !== slug),
    });

  const shared = { ...props, toast, docSlug, doc, setDoc, docSlug, form, handleTextInputSave };

  return (
    <Form context={form} className="document-update-form">
      {/* <Prompt when={isL} message="Are you sure?"> */}
      <AdminView
        className="document-update-view miq-container-fluid"
        title={<BackToStaffDocumentsLink className="btn-secondary-4" />}
        actions={<Actions {...shared} />}
      >
        <div className="my-3">
          <DocumentTitleInput onSave={handleTextInputSave} />
        </div>

        {/* FORM */}
        <div className="mb-3">
          <DocumentUpdateForm {...shared} />
        </div>

        {/* FILES */}

        <div className="d-flex align-items-center justify-content-between mb-3">
          <div className="fw-bold text-md">Fichiers</div>
          <div className="actions">
            <DocuFileUploadButton onSuccess={addFile} disabled={doc.is_read_only} className="btn-primary-2" />
          </div>
        </div>

        <DocuFileList document={doc} items={doc.files_data} onFileDelete={removeFile} />
      </AdminView>
      {/* </Prompt> */}
    </Form>
  );
}
