import Form, { FormProvider } from '@miq/form';

import { isRequired } from '@miq/utils';

export default function DocumentUpdateForm({ docSlug, ...props }) {
  const { form = isRequired('form'), doc, handleTextInputSave } = props;

  return (
    <FormProvider value={form}>
      <div className="document-field d-grid grid-lg-12 mb-2">
        <Form.Label value="Reference" className="span-12 mb-1" />
        <Form.TextInput
          name="reference"
          onSave={handleTextInputSave}
          autoComplete="off"
          className="span-12 span-md-4"
          placeholder="Ajouter une reference ..."
          disabled={doc.is_read_only}
        />
      </div>

      <div className="document-field d-grid grid-1 grid-xl-12 mb-2">
        <Form.Label value="Description" className="span-12 mb-1" />

        <div className="span-lg-8">
          <Form.TextAreaX
            name="description"
            onSave={handleTextInputSave}
            placeholder="Ajouter une description ..."
            disabled={doc.is_read_only}
          />
        </div>
      </div>
    </FormProvider>
  );
}
