import { Icons, Button, ImgSquare, Img, ConfirmIconButton } from '@miq/components';
import { Table, ItemTable, IconButton } from '@miq/components';
import { getImgUrl } from '@miq/utils';

import './docufile-grid.scss';
import { FileDeleteIconButton } from './components';
import { isArray } from 'lodash';

const getIcon = (file) => {
  switch (file.ext) {
    case '.jpg':
    case '.jpeg':
    case '.png':
      return <ImgSquare {...file} />;

    default:
      const Icon = Icons.getFileIcon(file.ext);
      return <Icon className="icon mb-1" />;
  }
};

export function DocuFileList({ items, ...props }) {
  if (!isArray(items)) return null;

  if (!items.length > 0)
    return <div className="w-100 text-center py-4 bg-gray-100 rounded">Ce document n'a pas de fichiers ajoutés.</div>;

  return (
    <div className="docufile-list border-1 rounded mb-3" style={{ overflowX: 'scroll' }}>
      <ItemTable
        items={items}
        renderItem={(file) => {
          return (
            <Table.Tr className="docufile-list-item border-bottom" key={file.slug}>
              <Table.Td>
                <a href={getImgUrl(file.src)} className="docufile-btn" target="_blank">
                  <div className="d-flex">
                    <span className="docufile-icon-wrapper d-none d-md-block me-1">{getIcon(file)}</span>
                    <div className="">
                      <div>{file.name_truncated}</div>
                      <span className="text-sm text-muted">{file.size}</span>
                    </div>
                  </div>
                </a>
              </Table.Td>

              <Table.Td align="right">
                <div className="actions">
                  <FileDeleteIconButton {...props} {...{ file }} />
                </div>
              </Table.Td>
            </Table.Tr>
          );
        }}
        className="w-100"
      />
    </div>
  );
}
