import { createRef, useContext } from "react";
import PropTypes from "prop-types";

import { FileInput, IconButton, Icons, ToastCtx } from "@miq/components";

import { docufileServices } from "./utils";

const propTypes = {
    onSuccess: PropTypes.func,
};

const SuccesMessage = ({ name_truncated }) => (
    <div>
        <span className="fw-bold d-block">File Added</span>
        {name_truncated}
    </div>
);

export default function DocuFileUploadButton({ onSuccess, ...props }) {
    const toast = useContext(ToastCtx);
    let uploadRef = createRef();

    function uploadFiles(evt) {
        // TODO: Ensure this filename has at most 100 characters

        Array.from(evt.target.files).forEach((file) => {
            docufileServices
                .postFile(file)
                .then((data) => {
                    if (onSuccess) onSuccess(data);
                    if (toast)
                        toast.success({
                            component: <SuccesMessage {...data} />,
                        });
                })
                .catch((err) => {
                    // console.log(err);
                    if (toast) toast.error({ message: "File upload failed.\nPlease try again." });
                });
        });
    }

    return (
        <>
            <IconButton
                Icon={Icons.PaperClip}
                label="Joindre"
                title="Joindre des fichiers à ce document"
                {...props}
                onClick={() => uploadRef.click()}
            />
            <FileInput onChange={uploadFiles} ref={(file) => (uploadRef = file)} />
        </>
    );
}

DocuFileUploadButton.propTypes = propTypes;
